import React from 'react';
import { Card, Badge } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import { lang } from '../../../../lang/lang';

let nf = Intl.NumberFormat();

const ActivoDetalle = (data) => {
    const {idx, de, total, avance, bg, color, tp} = data
    let clase = "col-xl-12"
    if( tp > 0 ) clase = "col-xl-4"
    return (
        <React.Fragment>
            <div className={clase}>
                <h6 className="align-items-center float-left" style={{fontSize: '0.75em'}}>
                    <Link to={'/app/tablero/2/' +idx +'/p'}>
                        <span  style={{co6lor: color}}>{de}</span>
                    </Link> 
                </h6>
                <h6 className="align-items-center float-right" style={{fontSize: '0.75em'}} >
                    {nf.format(total)}
                </h6>
                <div className="progress m-t-30  m-b-20" style={{ height: '6px' }} title={avance+'%'}>
                    <div
                        className="progress-bar progress-c-theme"
                        role="progressbar"
                        style={{ width: avance+'%', background: bg }}
                        aria-valuenow={avance}
                        aria-valuemin="0"
                        aria-valuemax="100"
                    />
                </div>
            </div> 
        </React.Fragment>
    );
};
export default ActivoDetalle;