import React from 'react';
import { Card, Badge } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { lang } from '../../../../lang/lang';

import ActivoDetalle from './ActivoDetalle'




const ActivoMaster = (data) => {
    const {idx, de, total, avance, bg, color, cli, tp} = data
    let detalle1 = []
    let detalle2 = []
    let detalle3 = []
    let pp = 0;
    for( let i=0; i < cli.length ; i++ ){
        if( tp > 0 ){
            if( pp == 0 ){
                detalle1.push(<ActivoDetalle 
                    key={i}
                    idx={i+1}
                    de={cli[i].de} 
                    total={cli[i].total} 
                    avance={cli[i].avance} 
                    bg={bg} 
                    tp={tp}
                />)
                pp += 1
            }else if( pp == 1 ){
                detalle2.push(<ActivoDetalle 
                    key={i}
                    idx={i+1}
                    de={cli[i].de} 
                    total={cli[i].total} 
                    avance={cli[i].avance} 
                    bg={bg} 
                    tp={tp}
                />)
                pp += 1
            }else{
                detalle3.push(<ActivoDetalle 
                    key={i}
                    idx={i+1}
                    de={cli[i].de} 
                    total={cli[i].total} 
                    avance={cli[i].avance} 
                    bg={bg} 
                    tp={tp}
                />)
                pp += 0
            }
        }else
            detalle1.push(<ActivoDetalle 
                key={i}
                idx={i+1}
                de={cli[i].de} 
                total={cli[i].total} 
                avance={cli[i].avance} 
                bg={bg} 
                tp={tp}
            />)
    }

    const out = []
    if( tp > 0 )
        out.push(<div className="row">{detalle1} {detalle2} {detalle3}</div>)
    else
        out.push(<div className="row">{detalle1}</div>)

    return (
        <React.Fragment>
            <Card>
                <Card.Header style={{background: color}}>
                    <Card.Title as="h5" style={{ color: color, height: '6px',width: '100%'}}>
                        <div className="row align-items-center justify-content-center" 
                        style={{ width: '100%'}} >
                            <div className="col-auto">
                                <Link to={'/app/tablero/1/' +idx +'/p'}>
                                <Badge variant="warning" style={{ background: bg}}><span  style={{color: color}}>{de}</span></Badge> 
                                </Link>       
                            </div>
                            <div className="col text-right" style={{fontSize: '0.8em'}}>
                                <Badge variant="warning"  style={{ background: color}}>{avance}%</Badge>       
                            </div>
                          
                        </div>
                    </Card.Title>
                    <div className="row align-items-center justify-content-center m-b-20" 
                    style={{ width: '100%', height: '6px' }}>
                        <div className="progress m-t-30 m-b-20" 
                        style={{ width: '100%', height: '6px' }} title={avance +'%'}>
                            <div
                                className="progress-bar progress-c-theme"
                                role="progressbar"
                                style={{ width: avance +'%', background: '#000' }}
                                aria-valuenow={avance}
                                aria-valuemin="0"
                                aria-valuemax="100"
                            />
                        </div>
                    </div>
                </Card.Header>
                <Card.Body>
                    {out}
                </Card.Body>
            </Card>
        </React.Fragment>
    );
};
export default ActivoMaster;