import React from 'react';
import { Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import DonnaChart from '../../../../components/nvd/DonnaChart';
import {lang} from '../../../../lang/lang';
import {activos} from './x'

let data = []
for(let i=0; i < activos.length ;i++){
    let k = 0
    for(let j=0; j < activos[i].cli.length ;j++)
        k += parseInt(activos[i].cli[j].total)
    data.push({key: activos[i].de, color: activos[i].bg, y: k })
}

const DashDefault = () => {
    return (
        <React.Fragment>
            <Card isOption>
                <Card.Header>
                    <Card.Title as="h5">Clase de Activo</Card.Title>
                </Card.Header>
                <Card.Body className="text-center">
                    <DonnaChart data={data} />
                </Card.Body>
            </Card>
        </React.Fragment>
    );
};
export default DashDefault;