import React from 'react';
import NVD3Chart from 'react-nvd3';

const PieDonutChart = (promp) => {
const {data} = promp
    return <NVD3Chart 
        id="chart" 
        height={300} 
        showLegend={ true } 
        showLabels={ true } 
        type="pieChart" 
        datum={data} 
        x="key" y="y" labelType="percent" />;
};
export default PieDonutChart;
