import React from 'react';
import { Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import {activos} from './x'
import ActivoMaster from './ActivoMaster'

const Activo = (promp) => {
    const {tp, id} = promp

    let master = []
    if( parseInt(tp) > 0 ){
        for( let i=0; i < activos.length ; i++ ){
            if( parseInt(id) -1 == i  )
            master.push(<Col md={12} ><ActivoMaster 
                key={i}
                idx={i+1} 
                de={activos[i].de} 
                total={activos[i].total} 
                avance={activos[i].avance} 
                bg={activos[i].bg} 
                color={activos[i].color}
                cli={activos[i].cli}
                tp={parseInt(tp)}
            /></Col>)
        }
    }else{
        for( let i=0; i < activos.length ; i++ ){
            master.push(<Col md={3} xl={3}><ActivoMaster 
                key={i}
                idx={i+1}
                de={activos[i].de} 
                total={activos[i].total} 
                avance={activos[i].avance} 
                bg={activos[i].bg} 
                color={activos[i].color}
                cli={activos[i].cli}
                tp={parseInt(tp)}
            /></Col>)
        }
    }

    return (
        <React.Fragment>
            {master}
        </React.Fragment>
    );
};
export default Activo;