import React from 'react';
import { Card } from 'react-bootstrap';
import BarsHorizontalChart from '../../../../components/nvd/BarsHorizontalChart';
import {lang} from '../../../../lang/lang';
import {activos} from './x'

// function generateNumber(min, max) { return Math.floor(Math.random() * (max - min + 1) + min); }
let graph2 = []
for(let i=0; i < activos.length ;i++){
    let aux = []
    for(let j=0; j < activos[i].cli.length ;j++)
        aux.push( {x: activos[i].cli[j].de, y: parseInt(activos[i].cli[j].total)} );
    graph2.push({values: aux, key: activos[i].de, color: activos[i].bg, area: true})
}

const Clientes = (promp) => { 
    const {tp, id} = promp 
    if( parseInt(id) > 0 ){
        graph2 = []
        let i = parseInt(id) - 1
        let aux = []
        for(let j=0; j < activos[i].cli.length ;j++)
            aux.push( {x: activos[i].cli[j].de, y: parseInt(activos[i].cli[j].total)} );
        graph2.push({values: aux, key: activos[i].de, color: activos[i].bg, area: true})
    }

    return (
        <React.Fragment>
            <Card isOption>
                <Card.Header>
                    <Card.Title as="h5">{lang.clientes}</Card.Title>
                </Card.Header>
                <Card.Body className="text-center">
                    <BarsHorizontalChart tp={tp} id={id} vct={graph2} />
                </Card.Body>
            </Card>
        </React.Fragment>
    );
};
export default Clientes;