/**
 * Crea un grafico de barras horizontales
 */
// import React from 'react';
import NVD3Chart from 'react-nvd3';

const BarsHorizontalChart = (promp) => {
    const {vct} = promp;

    return <NVD3Chart 
        key={'1'} 
        type="multiBarHorizontalChart"
        datum={vct}
        tooltip={{ enabled: true }} 
        showControls={false}
        stacked 
        margin={{left: 100}}
        showLegend={false}
        height={360}  
    />;
};
export default BarsHorizontalChart;
