
import React from 'react';
import { Card } from 'react-bootstrap';
// import { Link } from 'react-router-dom';
import { MapContainer, TileLayer, useMap, Marker, Popup, Polygon, useMapEvents } from 'react-leaflet';
import '../../assets/scss/leaflet_1.8.0.scss';
import geoColombia from '../../assets/json/colombia.geo.json';
import BorderLeaflet from './BorderLeaflet'

import {activos} from '../../views/tablero/DashDefault/partes/x';

// dibuja los departamentos
let poli2 = []
const polls = geoColombia.features;
for(let i=0; i < polls.length;i++){
    let poli = []
    if( polls[i].geometry.coordinates.length == 1 ){
        let poll = polls[i].geometry.coordinates[0];
        for(let j=0; j < poll.length; j++){
            let pol = poll[j];
            poli.push([pol[1],pol[0]]);
        }
        poli2.push(poli);
    }else{
        for(let k=0; k < polls[i].geometry.coordinates.length; k++){
            let poll = polls[i].geometry.coordinates[k][0];
            let poli = []
            for(let j=0; j < poll.length; j++){
                let pol = poll[j];
                poli.push([pol[1],pol[0]]);
            }
            poli2.push(poli);
        }
    }
}




function generateNumber(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min)/100;
}




const MapLeaflet = (param) => {
    const {tp, id, color, w, h, pos} = param;
    const sty = {
        width: w, 
        height: h
    }

    const items = []

    function LocationMarker() {
        const map = useMapEvents({
          click() {
            console.log('parcutirimicaro ... ');
            alert('paratem');
          }
        })
        return (
            <Marker position={pos}>
                <Popup>You are here</Popup>
            </Marker>
        );
    }

    let y = 0;
    if( true ){

    if( parseInt(tp) > 0 ){
        let col = activos[parseInt(id) -1].bg
        for( const [index, value] of poli2.entries() ){
            /*
             // leer 'op' de la base de datos

            */
            const op = generateNumber(0, 100) 
            const param = {
                color: '#000000',  
                weight: 0.5, 
                fill: true, 
                fillColor: col,
                fillOpacity: op
            }
            items.push(<BorderLeaflet key={index} options={param} puntos={value} accion={null} />)
            y += 1
        }
    }else
        for( const [index, value] of poli2.entries() ){
            const op = generateNumber(0, 100)  // leer de base de datos
            const param = {
                color: '#000000',  
                weight: 0.5, 
                fill: true, 
                fillColor: color,
                fillOpacity: op
            }
            items.push(<BorderLeaflet key={index} options={param} puntos={value} accion={null} />)
            y += 1
        }      

    } // true




    return (
        <React.Fragment>
            <Card>
                <MapContainer center={pos} zoom={5} scrollWheelZoom={false} 
                        style={sty}>
                    <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
                    <Marker position={pos}>
                        <Popup>
                            Locación seleccionada correctamente ...
                        </Popup>
                    </Marker>
                    {items}
                    <LocationMarker />
                </MapContainer>             
            </Card>
        </React.Fragment>
    );
};
export default MapLeaflet;

